<template>
    <div class="container">
        <div class="content-title">
            <router-link to="/questionnaireList"><img src="../assets/image/arr-back.png" alt="" width="20"></router-link>
            <span>{{ questionnaireData == null ? 'Loading' : questionnaireData.title }}</span>
        </div>
        <div class="content-body" v-loading="loading">
            <div class="question" v-if="questionnaireData != null" v-for="(item, index) in questionnaireData.item">
                <el-divider v-if="item.prefix != null && !item.text.startsWith('1.')" />
                <div class="question-prefix">{{ item.prefix }}</div>
                <p class="question-text">{{ item.text }}</p>
                <div class="question-choice" v-if="item.type == 'choice'">
                    <el-radio-group class="ml-4" v-if="item.choiceResponse != null" v-model="responseItems[index]">
                        <el-radio v-for="choice in item.choiceResponse.compose.include[0].concept" :label="choice.code" size="large">{{ choice.display }}</el-radio>
                    </el-radio-group>
                </div>
                <div class="question-integer" v-else-if="item.type == 'integer'">
                    <el-input-number :min="1" :max="10" />
                </div>
            </div>
            <el-button type="primary" id="submit" @click="testQuestionnaireData">Submit</el-button>
        </div>
    </div>
</template>

<script>
import questionnaires from '@/components/Questionnaires.vue'
import { ElMessage } from 'element-plus'
import { v4 as uuidv4 } from 'uuid';

export default {
    name: 'QuestionnaireView',
    data() {
        return {
            loading: false,
            loadIndex: 0,
            questionnaireData: null,
            scaleData: null,
            responseItems: [],
            totalScore: 0,
            questionnaireResponse: {
                "resourceType": "QuestionnaireResponse",
                "identifier": [{
                    "value": ""
                }],
                "questionnaire": "",
                "status": "completed",
                "subject": {
                    "reference": ""
                },
                "authored": "",
                "author": {
                    "reference": ""
                },
                "item": [],
                // "text": {
                //     "status": "generated",
                //     "div": ""
                // }
            },
            observation: {
                "resourceType": "Observation",
                "identifier": [{
                    "value": ""
                }],
                "status": "final",
                "category": [{
                    "coding": [{
                        "system": "http://terminology.hl7.org/CodeSystem/observation-category",
                        "code": "survey",
                        "display": "survey"
                    }]
                }],
                "subject": {
                    "reference": "",
                    "display": ""
                },
                "performer": [{
                    "reference": "",
                    "display": ""
                }],
                "derivedFrom": {
                    "reference": ""
                },
                "code": {
                    "text": ""
                },
                "effectiveDateTime": "",
                "valueQuantity": {
                    "value": 0
                },
                "interpretation": [{
                    "coding": [{
                        "display": ""
                    }],
                    "text": ""
                }],
                "component": []
            }, 
            valueSets: {}
        }
    },
    props: ['client', 'patient', 'practitioner'],
    created() {
        console.log('=== Questionnaire page ===')
        var id = this.$route.params.id
        console.log(id)
        if (id != null && id != '') {
            this.loadQuestionnaireData(id)
            // console.log(questionnaires.QUESTIONNAIRE_DATA[id])
        }
        else {
            this.$router.push("/questionnaireList")
        }
    },
    methods: {
        loadQuestionnaireData(name) {
            this.loading = true
            // Load questionnaire itself
            this.client.request('Questionnaire?identifier=' + name).then(data => {
                console.log(data)
                if (data.total == 0) {
                    this.createQuestionnaire(name)
                    return
                }
                this.questionnaireData = data.entry[0].resource
                this.renderChoices()
            })
            .catch(error => {
                console.log(error)
                this.questionnaireData = {
                    title: 'Server error, please try again later.'
                }
            })
        },
        createQuestionnaire(name) {
            console.log('create Questionnaire...')
            this.client.update(questionnaires.QUESTIONNAIRE_DATA[name].questionnaire).then(res => {
                console.log(res)
                this.loadIndex = 0
                this.questionnaireData = res
                for (var item in questionnaires.QUESTIONNAIRE_DATA[name].valueSet) {
                    this.client.update(questionnaires.QUESTIONNAIRE_DATA[name].valueSet[item]).then(res2 => {
                        this.loadIndex++
                        if (this.loadIndex == questionnaires.QUESTIONNAIRE_DATA[name].valueSet.length) {
                            this.renderChoices()
                        }
                    })
                }
            })
            .catch(eror => {
                this.questionnaireData = {
                    title: 'Server error, please try again later.'
                }
            })
        },
        renderChoices() {
            for (var a = 0; a < this.questionnaireData.item.length; a++) {
                if (this.questionnaireData.item[a].type == 'choice') {
                    // Load choices in each question
                    this.responseItems.push(null)
                    this.client.request(this.questionnaireData.item[a].answerOption[0].valueReference.reference).then(res => {
                        // console.log(res)
                        for (var b = 0; b < this.questionnaireData.item.length; b++) {
                            if (this.questionnaireData.item[b].type == 'choice' && 
                                this.questionnaireData.item[b].answerOption[0].valueReference.reference == 'ValueSet/' + res.id) {
                                this.questionnaireData.item[b].choiceResponse = res
                            }
                        }
                    })
                }
                else if (this.questionnaireData.item[a].type == 'reference') {
                    // Load scales and descriptions
                    this.responseItems.push(null)
                    this.client.request(this.questionnaireData.item[a].answerOption[0].valueReference.reference).then(re => {
                        this.scaleData = re.compose.include[0].concept
                    })
                    
                }
                else {
                    this.responseItems.push(null)
                }
            }
            this.loading = false
        },
        submitQuestionnaireResponse() {
            this.loading = true

            this.totalScore = 0
            this.questionnaireResponse.item = []
            var currentTime = new Date()
            var identifier = uuidv4()
            // Initialize QuestionnaireResponse
            this.questionnaireResponse.identifier[0].value = 'QUE-RESP-' + identifier
            this.questionnaireResponse.questionnaire = 'Questionnaire/' + this.questionnaireData.name
            this.questionnaireResponse.authored = currentTime.getFullYear() + '-' + (currentTime.getMonth() < 9 ? '0'+(currentTime.getMonth()+1) : (currentTime.getMonth()+1)) + '-' + (currentTime.getDate() < 10 ? '0'+currentTime.getDate() : currentTime.getDate())
            this.questionnaireResponse.subject.reference = 'Patient/' + this.patient.id
            this.questionnaireResponse.author.reference = 'Practitioner/' + this.practitioner.id
            // Initialized Observation
            this.observation.identifier[0].value = 'QUE-OBSV-' + identifier
            this.observation.code.text = this.questionnaireData.title + ' Result'
            this.observation.subject.reference = 'Patient/' + this.patient.id
            this.observation.performer[0].reference = 'Practitioner/' + this.practitioner.id
            this.observation.effectiveDateTime = currentTime
            // Adding QuestionnaireResponse items & calculating score
            if (this.$route.params.id == 'QUE-SF36') {
                for (var a = 0; a < this.questionnaireData.item.length; a++) {
                    if (this.questionnaireData.item[a].type == 'choice') {
                        if (this.responseItems[a] == null) {
                            ElMessage({
                                message: 'Question ' + (a+1) + ' is not being answered!',
                                type: 'warning',
                            })
                            this.loading = false
                            return
                        }
                        this.questionnaireResponse.item.push({
                            "linkId": this.questionnaireData.item[a].linkId,
                            "answer": {
                                "valueInteger": parseInt(this.responseItems[a]),
                            }
                        })
                    }
                    var scoreList = [0, 0, 0, 0, 0, 0, 0, 0]
                    scoreList[0] = (parseInt(this.responseItems[2]) + parseInt(this.responseItems[3]) + parseInt(this.responseItems[4]) + parseInt(this.responseItems[5]) + parseInt(this.responseItems[6]) + parseInt(this.responseItems[7]) + parseInt(this.responseItems[8]) + parseInt(this.responseItems[9]) + parseInt(this.responseItems[10]) + parseInt(this.responseItems[11])) / 10
                    scoreList[1] = (parseInt(this.responseItems[12]) + parseInt(this.responseItems[13]) + parseInt(this.responseItems[14]) + parseInt(this.responseItems[15])) / 4
                    scoreList[2] = (parseInt(this.responseItems[16]) + parseInt(this.responseItems[17]) + parseInt(this.responseItems[18])) / 3
                    scoreList[3] = (parseInt(this.responseItems[22]) + parseInt(this.responseItems[26]) + parseInt(this.responseItems[28]) + parseInt(this.responseItems[30])) / 4
                    scoreList[4] = (parseInt(this.responseItems[23]) + parseInt(this.responseItems[24]) + parseInt(this.responseItems[25]) + parseInt(this.responseItems[27]) + parseInt(this.responseItems[29])) / 5
                    scoreList[5] = (parseInt(this.responseItems[19]) + parseInt(this.responseItems[31])) / 2
                    scoreList[6] = (parseInt(this.responseItems[20]) + parseInt(this.responseItems[21])) / 2
                    scoreList[7] = (parseInt(this.responseItems[0]) + parseInt(this.responseItems[32]) + parseInt(this.responseItems[33]) + parseInt(this.responseItems[34]) + parseInt(this.responseItems[35])) / 5
                
                    this.observation.component = [{
                        "code": { "text": "Physical functioning" },
                        "valueQuantity": { "value": parseInt(scoreList[0]) }
                    }, {
                        "code": { "text": "Role functioning - physical" },
                        "valueQuantity": { "value": parseInt(scoreList[1]) }
                    }, {
                        "code": { "text": "Role functioning - emotional" },
                        "valueQuantity": { "value": parseInt(scoreList[2]) }
                    }, {
                        "code": { "text": "Energy/fatigue" },
                        "valueQuantity": { "value": parseInt(scoreList[3]) }
                    }, {
                        "code": { "text": "Emotional well-being" },
                        "valueQuantity": { "value": parseInt(scoreList[4]) }
                    }, {
                        "code": { "text": "Social functioning" },
                        "valueQuantity": { "value": parseInt(scoreList[5]) }
                    }, {
                        "code": { "text": "Pain" },
                        "valueQuantity": { "value": parseInt(scoreList[6]) }
                    }, {
                        "code": { "text": "General health" },
                        "valueQuantity": { "value": parseInt(scoreList[7]) }
                    }]
                }
            }
            else {
                for (var a = 0; a < this.questionnaireData.item.length; a++) {
                    if (this.questionnaireData.item[a].type == 'choice') {
                        if (this.responseItems[a] == null) {
                            ElMessage({
                                message: 'Question ' + (a+1) + ' has not answered!',
                                type: 'warning',
                            })
                            this.loading = false
                            return
                        }
                        this.questionnaireResponse.item.push({
                            "linkId": this.questionnaireData.item[a].linkId,
                            "answer": {
                                "valueInteger": parseInt(this.responseItems[a]),
                            }
                        })
                        this.totalScore += parseInt(this.responseItems[a])
                    }
                }
            }
            
            // this.questionnaireResponse.text.div = "<div>" + this.questionnaireData.title.split(' ')[0] + '&' + this.totalScore + "</div>"
            this.observation.valueQuantity.value = this.totalScore
            // Matching explanation & complete Observation
            for (var a = 0; a < this.scaleData.length; a++) {
                var codeStr = this.scaleData[a].code.split('-')
                if (this.totalScore >= parseInt(codeStr[0]) && this.totalScore <= parseInt(codeStr[1])) {
                    this.observation.interpretation[0].coding[0].display = this.scaleData[a].display
                    this.observation.interpretation[0].text = this.scaleData[a].designation[0].value
                    break
                }
            }
            console.log(this.observation)
            // Save to the server
            this.client.create(this.questionnaireResponse).then(res => {
                console.log(res)
                this.observation.derivedFrom.reference = "QuestionnaireResponse/" + res.id
                this.client.create(this.observation).then(res2 => {
                    console.log(res2)
                    this.loading = false
                    this.$router.push('/result/' + identifier)
                })
            })
        },

        testQuestionnaireData() {
            console.log(this.responseItems)
            this.submitQuestionnaireResponse()
        },
    }
}
</script>

<style scoped>
.question {
    text-align: left;
    font-size: 16px;
}
.question .question-text {
    font-weight: bold;
    margin: 18px 0 0 0;
}
.el-radio {
    width: 100%;
    height: auto!important;
    min-height: 25px!important;
    margin-top: 3px;
    align-items: baseline;
    white-space: inherit;
}
.el-radio span {
    white-space: normal!important;
}
.question :deep(.el-radio__label) {
    font-size: 16px!important;
}
#submit {
    margin-bottom: 120px;
}

.question-prefix {
    color: var(--blue-main);
}
</style>