<template>
    <div class="container">
        <div class="content-title">
            <router-link to="/resultList"><img src="../assets/image/arr-back.png" alt="" width="20"></router-link>
            <span>{{ observation == null ? 'Loading' : observation.code.text }}</span>
        </div>
        <div class="content-body" v-loading="loading">
            <div class="card">
                <p class="interpretation-title">{{ observation == null ? '...' : observation.interpretation[0].coding[0].display }}</p>
                <div class="scale"><img src="../assets/image/question-sm.png" alt="" width="28" @click="scaleDialog = true"></div>
                <p class="interpretation-score-sf36" v-if="observation.component != null">
                    <div v-for="item in observation.component">
                        <span class="sf36-title">{{ item.code.text }}</span>
                        <span class="sf36-score">{{ item.valueQuantity == null ? 0 : parseInt(item.valueQuantity.value) }}</span>
                    </div>
                </p>
                <p class="interpretation-score" v-else >{{ observation == null ? '-' : observation.valueQuantity.value }}</p>
                <p class="interpretation-detail">
                    <span class="time">
                        <img src="../assets/image/time-sm.png" alt="" width="16">
                        <span>{{ questionnaireResponse == null ? '...' : questionnaireResponse.authored }}</span>
                    </span>
                    <span class="prac">
                        <img src="../assets/image/doctor-sm.png" alt="" width="16">
                        <span>{{ practitionerName }}</span>
                    </span>
                </p>
            </div>
            <div class="interpretation-text">
                {{ observation == null ? '...' : observation.interpretation[0].text }}
            </div>

            <div class="submission" @click="openSubmission()">View Submission</div>
            <router-link to='/'><el-button type="primary" id="homepage" @click="">Homepage</el-button></router-link>
        </div>
    
        <el-dialog v-model="submissionDialog" title="Submission" width="80%" align-center>
            <div class="dialog-body">
                <div class="question" v-if="questionnaireData != null" v-for="item in questionnaireData.item">
                    <p class="question-text" v-if="item.type != 'reference'">{{ item.text }}</p>
                    <div class="question-choice" v-if="item.type != 'reference'">
                        <el-radio size="large">{{ item.submission }}</el-radio>
                    </div>
                </div>
            </div>
        </el-dialog>

        <el-dialog v-model="scaleDialog" title="Scale" width="80%" align-center>
            <div class="dialog-body">
                <div class="question" v-if="scale != null" v-for="item in scale.compose.include[0].concept">
                    <p class="question-text txt-blue">{{ item.code }} : {{ item.display }}</p>
                    <div class="question-choice" v-if="item.type != 'reference'">
                        {{ item.designation[0].value }}
                    </div>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'ResultView',
    data() {
        return {
            loading: false,
            resultTitle: '',
            questionnaireResponse: null,
            observation: null,
            practitionerName: '',
            submissionDialog: false,
            scaleDialog: false,
            questionnaireData: null,
            valueSets: {},
            scale: null
        }
    },
    props: ['client', 'patient', 'practitioner'],
    created() {
        console.log('=== Result page ===')
        var id = this.$route.params.id
        if (id != null && id != '') {
            this.loadResult(id)
        }
        else {
            this.$router.push("/questionnaireList")
        }
    },
    methods: {
        loadResult(id) {
            this.loading = true
            this.client.request('QuestionnaireResponse?identifier=QUE-RESP-' + id).then(data => {
                console.log(data)
                if (data.total > 0) {
                    this.questionnaireResponse = data.entry[0].resource
                    this.client.request(this.questionnaireResponse.questionnaire).then(res => {
                        this.questionnaireData = res
                        this.renderSubmission()
                        this.renderScale()
                    })
                }
            })
            this.client.request('Observation?identifier=QUE-OBSV-' + id).then(data2 => {
                console.log(data2)
                if (data2.total > 0) {
                    this.observation = data2.entry[0].resource
                    this.client.request(this.observation.performer[0].reference).then(res => {
                        this.practitionerName = res.name[0].prefix[0] + res.name[0].given[0] + ' ' + res.name[0].family
                        this.loading = false
                    })
                }
            })
        }, 
        renderSubmission() {
            for (var a = 0; a < this.questionnaireData.item.length; a++) {
                if (this.questionnaireData.item[a].type == 'reference') {
                    continue
                }
                if (this.valueSets[this.questionnaireData.item[a].answerOption[0].valueReference.reference] == null) {
                    this.client.request(this.questionnaireData.item[a].answerOption[0].valueReference.reference).then(res => {
                        this.valueSets['ValueSet/' + res.id] = res
                        // this.renderSubmission()
                        return
                    })
                }
                else {
                    this.questionnaireData.item[a].submission = this.getChoiceText(this.questionnaireResponse.item[a].answer[0].valueInteger, this.valueSets[this.questionnaireData.item[a].answerOption[0].valueReference.reference])
                }
            }
        },
        openSubmission() {
            console.log(this.questionnaireResponse)
            for (var a = 0; a < this.questionnaireResponse.item.length; a++) {
                this.questionnaireData.item[a].submission = this.getChoiceText(this.questionnaireResponse.item[a].answer[0].valueInteger, this.valueSets[this.questionnaireData.item[a].answerOption[0].valueReference.reference])
            }
            this.submissionDialog = true
        },
        renderScale() {
            this.client.request(this.questionnaireData.item[this.questionnaireData.item.length - 1].answerOption[0].valueReference.reference).then(res => {
                this.scale = res
                console.log(this.scale)
            })
        },
        getChoiceText(index, vs) {
            for (var i = 0; i < vs.compose.include[0].concept.length; i++) {
                if (index == parseInt(vs.compose.include[0].concept[i].code)) {
                    return vs.compose.include[0].concept[i].display
                }
            }
            return ''
        },
    }
}
</script>

