<template>
    <div class="container">
        <div class="content-title">
            <router-link to="/"><img src="../assets/image/arr-back.png" alt="" width="20"></router-link>
            <span>Questionnaire Results</span>
        </div>
        <div class="content-body" v-loading="loading">
            <div v-for="(item, index) in resultList">
                <router-link :to="'/result/' + item.resource.identifier.value.substr(9)">
                    <div class="card card-flex">
                        <div class="card-left">
                            <div class="card-title">
                                <img src="../assets/image/result-lg.png" alt="" width="24">
                                <span>{{ observationList[index].resource.code.text.split(' ')[0] }}</span>
                            </div>
                            <div class="card-desc">
                                {{ item.resource.authored }}
                            </div>
                        </div>
                        <div class="card-right">
                            {{ observationList[index].resource.code.text.startsWith('SF') ? '-' : observationList[index].resource.valueQuantity.value }}
                        </div>
                    </div>
                </router-link>
            </div>
            <el-empty v-if="resultList.length < 1" description="No available result" />
        </div>
    </div>
</template>

<script>
export default {
    name: 'QuestionnaireListView',
    data() {
        return {
            loading: false,
            resultList: [],
            observationList: []
        }
    },
    props: ['client', 'patient', 'practitioner'],
    created() {
        console.log('=== Result list page ===')
        this.loadResultListData()
    },
    methods: {
        loadResultListData() {
            this.loading = true
            this.client.request('QuestionnaireResponse?subject=Patient/' + this.patient.id + '&_revinclude=Observation:derived-from').then(data => {
                console.log(data)
                if (data.total > 0) {
                    // this.resultList = data.entry
                    for (var a = 0; a < data.total; a++) {
                        this.resultList.push(data.entry[a])
                        this.observationList.push(null)
                    }
                    for (var b = data.total; b < data.entry.length; b++) {
                        console.log(data.entry[b].resource.derivedFrom[0].reference)
                        for (var i in this.resultList) {
                            if (this.resultList[i].resource.id == data.entry[b].resource.derivedFrom[0].reference.split('/')[1]){
                                this.observationList[i] = data.entry[b]
                                break
                            }
                        }
                    }
                }
                this.loading = false
            })
        }
    }
}
</script>