import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Auth from '../views/Auth.vue'
import Questionnaire from '../views/QuestionnaireView.vue'
import QuestionnaireList from '../views/QuestionnaireListView.vue'
import Result from '../views/ResultView.vue'
import ResultList from '../views/ResultListView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  // },
  {
    path: '/auth',
    name: 'auth',
    component: Auth
  },
  {
    path: '/questionnaire/:id',
    name: 'questionnaire',
    component: Questionnaire
  },
  {
    path: '/questionnaireList',
    name: 'questionnaireList',
    component: QuestionnaireList
  },
  {
    path: '/resultList',
    name: 'resultList',
    component: ResultList
  },
  {
    path: '/result/:id',
    name: 'result',
    component: Result
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
