<template>
  <Header :patientName="patientName" :patientSex="patientSex" :patientAge="patientAge" :practitioner="practitionerName"></Header>
  <div class="main-container">
    <router-view :client="client" :patient="patient" :practitioner="practitioner"/>
  </div>
</template>

<script>
import FHIR from 'fhirclient'
import Header from '@/components/Header.vue'
import '@/assets/style/global.css'

export default {
  data() {
    return {
      client: null,
      patientName: '',
      patientSex: '',
      patientAge: '',
      practitionerName: '',
      patient: null,
      pratcitioner: null
    }
  },
  components: {
    Header
  },
  mounted() {
    // Automatically check if already login when reload the page.
    console.log('Init FHIR...')
    FHIR.oauth2.ready()
    // .then(client => client.patient.read())
    .then(this.successHandler)
    .catch(this.errorHandler)
    console.log('Init complete.');
  },
  methods: {
    errorHandler(err) {
      // If not login, redirect to oAuth2 page.
      console.log("=== error ===")
      console.log(err)
      this.$router.push({path: '/auth'})
    },
    successHandler(client) {
      // If login already, initialize client for global use.
      console.log("=== success ===")
      this.client = client
      console.log(this.client)
      this.client.patient.read().then(res => {
        console.log(res)
        this.patient = res
        for (var a = 0; a < res.name.length; a++) {
          if (res.name[a].use == 'official') {
            this.patientName = res.name[a].given[0] + ' ' + res.name[a].family
          }
        }
        this.patientSex = res.gender
        var now = new Date()
        this.patientAge = now.getFullYear() - parseInt(res.birthDate.split("-")[0])
      })
      this.client.user.read().then(data => {
        console.log(data)
        this.practitioner = data
        this.practitionerName = data.name[0].prefix[0] + data.name[0].given[0] + ' ' + data.name[0].family
      })
    }
  }
}
</script>

